import ok from "@/assets/ok.png";
export default {
  data() {
    return {
      list:{},
      user_name:"姓名",
      id_card:"身份证号码",
      okIcon:ok,
      title: "您已认证通过!"
    };
  },
  mounted(){
    this.getUserDetaill();
    // 
  },
    methods: {
    getUserDetaill(){
       var that=this,
      formData={
        sign: that.$store.state.user.sign,
        access_token: that.$store.state.user.token
      }
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/personal/user-detail",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.list=response.data;
          that.list.id_card=response.data.id_card.replace(/^(.{4})(?:\w+)(.{3})$/, "$1********$2")
        })
        .catch(function(error) {
        console.log(error)
        });

    }
  }
};



